/* src/styles/ProjectDetail.css */

.project-detail {
    max-width: 800px;
    margin: 40px auto;
    padding: 30px;
    background: #1a1a1a;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    color: #fff;
    font-family: 'Arial', sans-serif;
  }
  
  .project-header {
    text-align: center;
    font-size: 2.5rem;
    color: #ffd700; /* Gold accent */
    margin-bottom: 20px;
  }
  
  .project-content {
    line-height: 1.6;
    font-size: 1.1rem;
  }
  
  .project-paragraph {
    margin-bottom: 20px;
  }
  
  .project-image {
    max-width: 80%;
    height: auto;
    display: block;
    margin: 20px auto;
    border: 3px solid #ffd700;
    border-radius: 8px;
  }
  
  .video-container {
    text-align: center;
    margin: 20px 0;
  }
  
  .video-container iframe {
    width: 100%;
    max-width: 560px;
    height: 315px;
    border-radius: 8px;
  }
  
  .model-viewer {
    display: block;
    width: 100%;
    height: 500px;
    margin: 20px auto;
    border: 3px solid #ffd700;
    border-radius: 8px;
  }
  
  .back-link {
    display: inline-block;
    margin-top: 30px;
    text-decoration: none;
    background-color: #ffd700;
    color: #1a1a1a;
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .back-link:hover {
    background-color: #e6c200;
  }
  