/* src/styles/ProjectList.css */

.project-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background: #1a1a1a; /* Dark theme */
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    color: #fff;
  }
  
  .project-title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
    color: #ffd700; /* Gold accent */
  }
  
  .project-list {
    list-style: none;
    padding: 0;
  }
  
  .project-item {
    margin-bottom: 15px;
    padding: 15px;
    border-radius: 8px;
    background: #222;
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  }
  
  .project-item:hover {
    transform: scale(1.02);
    box-shadow: 0px 4px 12px rgba(255, 215, 0, 0.3);
  }
  
  .project-link {
    text-decoration: none;
    color: inherit;
    display: block;
  }
  
  .project-name {
    font-size: 1.5rem;
    margin: 0;
    color: #ffd700; /* Gold title */
  }
  
  .project-desc {
    font-size: 1rem;
    color: #ccc;
    margin-top: 5px;
  }
  
  /* Quarter Break Section */
  .quarter-break {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin: 20px 0;
    padding: 10px;
    background: #444;
    border-radius: 5px;
    color: #ffd700;
  }
  